export const RECEIVE_POSTS = 'RECEIVE_POSTS'
let Utils = require('../utils/Utils');

function receivePosts(data) {
  if(!data){
    data = [];
  }
  return {
    type: RECEIVE_POSTS,
    postsByVenderPic: data
  }
}

